* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
}
.main-container {
  background-color: #c8b7f0;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.container {
  width: max(300px, 30%);
  background-color: white;
  color: black;
  padding: 1rem 3rem;
  border-radius: 20px;
  box-shadow: 1px 1px 11px 0px rgba(0, 0, 0, 0.75);
}
.form-container p {
  background-color: rgb(179, 80, 80);
  padding: 0.2rem 0.5rem;
  font-size: small;
  color: white;
  border-radius: 5px;
}
.sign-up-title {
  text-align: center;
  margin-bottom: 0.5rem;
}
.center {
  text-align: center;
}
.input-container {
  margin-top: 0.6rem;
  position: relative;
}
.input-container label {
  color: #797676;
  font-weight: 600;
  font-size: 0.8rem;
}
.input-box {
  display: block;
  padding: 0.5rem 1rem;
  margin-top: 0.6rem;
  width: 100%;
  border-radius: 5px;
  border: 1px solid grey;
}
.signUpBtn {
  width: 100%;
  padding: 0.6rem 0;
  border-radius: 5px;
  background-color: rgb(14, 14, 70);
  color: white;
  font-size: 1rem;
  font-weight: bold;
}
.eye-icon-container {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.eye-icon {
  position: absolute;
  right: 0.5rem;
  bottom: 0.5rem;
}
